@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
html{
  font-size: 62.5%;
  box-sizing: border-box;

}
body,#root{
  height: 100vh;
  margin: 0;
}
body , input , select , textarea, button {
  font-size: 1.6rem;
  font-family: Montserrat, Helvetica, Arial, sans-serif;
}
label{
  display: block;
}
a:hover{
  color: #ff8000;
}
a{
  text-decoration: none;
}
.grid-container{
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 5rem 1fr 5rem;
  grid-template-areas: "header" "main" "footer" ;
  height: 100%;
}
header{
  grid-area: header;
  background-color: #203040;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
header a{
  color: #fff;
  text-decoration: none;
}
main{
  grid-area: main;
}
footer{
  grid-area: footer;
  background-color: #203040;
  color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.content{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.main{
  -webkit-flex : 3 1 60rem;
          flex : 3 1 60rem;
}
.sidbar{
  -webkit-flex: 1 1 20rem;
          flex: 1 1 20rem;
}
/* products */
.products{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 0;
  margin: 0;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  list-style: none;
  transition: all 0.7s;
}
.products li{
  -webkit-flex: 0 1 29rem;
          flex: 0 1 29rem;
  height: 47rem;
  padding: 1rem;
}
.product{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 100%;

}
.product img{
  max-width: 37rem;
  max-height: 37rem;
}
.product-price{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}
.product-price div,
.product-price .button{
  text-align: center;
  -webkit-flex: 1 1;
          flex: 1 1;
}
.button:hover{
  border: 0.1rem solid #808080;
}
.product-price div{
  font-size: 2rem;
}
.button{
  padding: 1rem;
  border: 0.1rem #c0c0c0 solid;
  background-color: #f0f0f0;
  cursor: pointer;
}
.button-primary{
  background-color: #f0c040;
}
/* filter */
.filter{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 1rem;
  margin: 1rem;
  border-bottom: 1px solid #c0c0c0;
}
.filter div{
  -webkit-flex: 1 1;
          flex: 1 1;
} 
/* cart */
.cart{
  padding: 1rem;
  margin: 1rem;
  display: -webkit-flex;
  display: flex;
}
.cart-header{
  border-bottom: 0.1rem #c0c0c0 solid;
}
.cart-items{
  padding: 0;
  width: 100%;
  list-style: none;
}
.cart-items img{
  width: 5rem;
}
.cart-items li{
  display: -webkit-flex;
  display: flex;
}
.cart-items div{
  padding: 0.5rem;
}
.cart-items div:last-child{
  -webkit-flex: 1 1;
          flex: 1 1;
}
.right{
  text-align: right;
}
.total{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
}
.total > div{
  font-size: 2rem;
}
.total > div,
.total > button{
  -webkit-flex:1 1;
          flex:1 1
  
}
/* form */
form{
  width: 100%;
}
.form-container{
  width: 100%;
  padding: 0;
  list-style: none;
}
.form-container li{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 1rem;
}
input{
  padding: 1rem;
  border: .1rem #c0c0c0 solid;
}
/* produect Detaelies */

.product-details{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  max-height: 96vh;
}
.product-details img{
  max-height: 100vh;
  max-width: 46rem;
  margin: 0 auto;
}
.product-details-description{
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 1rem;
}
.close-modal{
  position: absolute;
  right: 1rem;
  top: 2.5rem;
  z-index: 1000;
}
